import "./burger.scss";

import { NavLink } from "react-router-dom";

const Burger = (props) => {
  let active = {
    color: "rgb(81 200 245)",
  };
  let disactive = {
    color: "black",
  };
  return (
    <div className="burger">
    <div className="nav-burger">
      <NavLink
        to="/development"
        style={({ isActive }) => (isActive ? active : disactive)}
      >
        
      </NavLink>

      <NavLink
        to="/support"
        style={({ isActive }) => (isActive ? active : disactive)}
      >
        
      </NavLink>

      <NavLink
        to="/portfolio"
        style={({ isActive }) => (isActive ? active : disactive)}
      >
        
      </NavLink>

      <NavLink
        to="/contact"
        style={({ isActive }) => (isActive ? active : disactive)}
      >
        
      </NavLink>
    </div>
  </div>
 )
};

export default Burger;
